import { AutoComplete, Input } from 'antd';
import React, { FC, useEffect, useState } from 'react';
import styles from './FormLocation.module.less';
import map from '../../../../assets/img/map/map.webp';
import { useActionsMapPoints } from '../../../../hooks/useActions/useActionsMapPoints';
import { useTypedSelector } from '../../../../hooks/useTypesSelector';
import { mapPointsSelector } from '../../../../store/selectors/selectors';

const FormLocation: FC = () => {
  const { error, isLoading } = useTypedSelector(mapPointsSelector);
  const { points } = useTypedSelector(mapPointsSelector);

  // Делаю запрос на получение меток карты из api
  const { fetchPoints } = useActionsMapPoints();

  useEffect(() => {
    fetchPoints();
  }, []);

  // Отфильтровываю метки, где нет данных о городе
  const filteredPoint = points.filter((cv) => !(cv.cityId === null));

  const options = filteredPoint.map((point) => {
    return {
      value: point.cityId!.name,
    };
  });

  return (
    <div className={styles.FormLocation}>
      <div className={styles.inputs}>
        <div className={styles.inputContainer}>
          <span className={styles.inputText}>Город</span>
          <Input placeholder="Начните вводить город..." className={styles.input} bordered={false} />
          <AutoComplete
            style={{ width: 200 }}
            options={options}
            filterOption={(inputValue, option) =>
              option!.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
            }
          />
        </div>
        <div className={styles.inputContainer}>
          <span className={styles.inputText}>Пункт выдачи</span>
          <Input placeholder="Начните вводить пункт..." className={styles.input} bordered={false} />
        </div>
      </div>
      <div className={styles.mapBlock}>
        <span>Выбрать на карте:</span>
        <img className={styles.map} src={map} alt="map" />
      </div>
    </div>
  );
};

export default FormLocation;
